                 background-color: yellow;
                 padding: 15px 30px 13px 10px;
                 color: black;
            }
        }
            .nav > li {
                @media screen and (max-width: 770px){
                    width: 89%;
                    border-bottom: 1px solid yellow;
                }
            }
        .nav > li > a {
            position: relative;
            display: block;
            padding: 13px 11px 9px 25px;
            color: white;
            line-height: 26px;
            font-family: "open_sansregular";
            font-size: 14.05px;
            text-transform: uppercase;
            @media screen and (max-width: 1300px){
                padding: 13px 6px 9px 17px;
            }
            @media screen and (max-width: 1000px){
                padding: 13px 4px 9px 13px;
                font-size: 13.05px;
            }
            @media screen and (max-width: 770px){
                 background-color: #161616;
            }
        }
        .nav > li:last-child > a {
            padding-right: 0;
        } 
        .navbar-right {
            width: 520px;
            background-color: #161616;
            padding: 9px 23px 4px 20px;
            a {
                padding-left: 20px;
                text-decoration: none;
                color: white;
                text-transform: uppercase;
                font-size: 14.03px;
                font-weight: 600;
                @media screen and (max-width: 1300px){
                    padding-left: 5px;
                    font-size: 13.03px;
                }
            }
            @media screen and (max-width: 1300px){
                width: 330px;
                padding: 9px 5px 4px 5px;
            }
             @media screen and (max-width: 1000px){
                width: 220px;
            }
             @media screen and (max-width: 770px){
                 display: none;
            }
        }
        .glyphicon.glyphicon-map-marker, span.findHotel {
            margin-left: 50px;
            color: white;
            text-transform: uppercase;
            @media screen and (max-width: 1300px){
                margin-left: 5px;
                font-size: 13.03px;
                 .innerSpan {
                     display: none;
                 }
            }
        }
        span.findHotel {
            font-family: "open_sanssemibold"; 
            margin-left: 5px;
        }
        @media screen and (max-width: 1000px){
             span.findHotel, .glyphicon.glyphicon-map-marker {
                 display: none;
             }
        }
}





